
document.addEventListener('DOMContentLoaded', () => {
  const storageKey = 'theme-preference';
  const firstVisitKey = 'first-visit';

  const isFirstVisit = !localStorage.getItem(firstVisitKey);

  if (isFirstVisit) {

    localStorage.clear();
    localStorage.setItem(firstVisitKey, 'true');
  }


  const getColorPreference = () => {
    const savedTheme = localStorage.getItem(storageKey);
    return savedTheme || 'dark';
  };

  const theme = {
    value: getColorPreference(),
  };

  const setPreference = () => {
    if (theme.value === 'light') {
      localStorage.setItem(storageKey, theme.value);
    } else {
      localStorage.removeItem(storageKey);
    }
    reflectPreference();
  };


  const reflectPreference = () => {
    document.documentElement.setAttribute('data-theme', theme.value);

    const icons = document.querySelectorAll('.theme-switch__icon');
    icons.forEach(icon => icon.classList.toggle("theme-switch__icon--active", icon.classList.contains(theme.value)));


    const logo = document.querySelector('.logo__img');
    if (logo) {
      logo.src = theme.value === "dark" ? "images/logo.svg" : "images/logo-white.svg";
    }
  };


  const onClick = () => {
    theme.value = theme.value === 'light' ? 'dark' : 'light';
    setPreference();
  };

  reflectPreference();

  const themeSwitch = document.querySelector('.theme-switch');
  if (themeSwitch) {
    themeSwitch.addEventListener('click', onClick);
  }


  window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', ({
    matches: isDark
  }) => {
    if (theme.value === 'dark') {
      theme.value = isDark ? 'dark' : 'light';
      setPreference();
    }
  });
});